.hour {
    position: relative;
    width: 100px;
    height: 150px;
    margin-bottom: 10px;
    border: 1px solid rgb(160, 160, 160);
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    padding: 10px;
    padding-top: 0px;
}

.hour:hover {
    background-color: darkcyan;
    cursor: pointer;
}

.containsEmployee {
    background-color: rgba(3, 163, 0, 0.35);
}

.hour-btn {
    background-color: #437C90;
    position: relative;
    font-size: 1rem;
    width: 100%;
    height: 1.5rem;
    border-radius: 1rem;
    text-align: center;
    vertical-align: middle;
    line-height: 1.5rem;
}

.hour-btn:active {
    -webkit-transform: translateY(.1rem);
        -ms-transform: translateY(.1rem);
            transform: translateY(.1rem);
}

.hour-btn:hover {
    cursor: pointer;
}

.hour li:hover {
    background-color: #B80C09;
    cursor: pointer;
}


