.carousel {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    margin-bottom: 10px;
}

.employeeBlock {
    width: 50px;
    height: 40px;
    border: 1px solid green;
    -webkit-box-sizing: border-box;
            box-sizing: border-box
}

.employeeBlock:not(:first-child) {
    margin-left: 5px;
}

.employeeBlock div:first-child:hover {
    cursor: pointer;
}


