@import './App.css';
@import './Calendar/Calendar.css';
@import './Carousel/Carousel.css';
@import './Hour/Hour.css';
@import './Navbar/Navbar.css';
@import './NewEmployeeForm/NewEmployeeForm.css';
@import './TimetablePage/TimetablePage.css';

html {
    font-family: 'Roboto', sans-serif;
    min-height: 100%;
}

body {
    min-height: 100%;
    background: -webkit-gradient(linear, left bottom, left top, from(#2A71B1), to(#0E3775));
    background: -webkit-linear-gradient(bottom, #2A71B1, #0E3775);
    background: -o-linear-gradient(bottom, #2A71B1, #0E3775);
    background: linear-gradient(to top, #2A71B1, #0E3775);
    padding-bottom: 1rem;
}


